import './Home.css'
import img from './playBlack3.png';
import imgReact from './react.png';
import imgReactHex from './hexagon.png';
import imgHtml from './html1.png';
import imgCss from './css.png';
import imgJs from './js.png';
import imgRedux from './redux.png';
import imgNode from './nodejs.png';
import {Container, Row, Col, Card, Modal} from 'react-bootstrap'
import { useState } from 'react'
import myself from '../../image/thinking.png';

export default function Home() {
    let technologies = ['html', 'css', 'javascript', 'react', 'redux', 'node'];
    let imagenes = [imgHtml, imgCss, imgJs,
    [imgReact, imgReactHex], imgRedux, imgNode]
    const [circles, setCircles] = useState(['circleHidden', 'circleHidden', 'circleHidden', 'circleHidden', 'circleHidden', 'circleHidden'])
    const [modal, setModal] = useState(false)

    document.body.style = `
    background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%);`
    
    return (
        <Container color="red" fluid>
            {/* <img src={myself} alt="think" style={{position: "absolute", right: 0, bottom: 0}}/> */}

            <Row className="justify-content-center">
                <Col sm={12} lg={8}  style={{height: 'auto'}}>
                    <Card style={{backgroundColor: 'rgba(0, 0, 0, 0.4)', color: 'white', marginTop: '20px'}} className="testMove text-center">
                        <Card.Body>
                        <h1>¡Welcome to my presentational page!</h1>
                        <h3>My name is Gonzalo Rodriguez, and you are about to see my development skills!</h3>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>

            
            <Row className="justify-content-center">
                <Col sm={12} style={{marginTop: '50px', display: 'flex', flexDirection: 'column', alignItems: 'center', height: '300px'}}>
                    <Card style={{backgroundColor: 'rgba(0, 0, 0, 0.0)', width: '300px', height: '300px', borderRadius: '50%'}} className="playImg"
                        onClick={e => {
                            let quitarFadeIn = (id) => {
                                setTimeout((i = id) => {
                                    let aux = circles;
                                    aux[i] = "circleHiddenAn";
                                    setCircles(s => ([...aux]))
                                }, 1000)
                            }
                            setModal(true)
                            setTimeout(() => {
                                let aux = circles;
                                aux[0] = "circleHiddenAn circleHiddenBox";
                                setCircles(s => ([...aux]))
                                quitarFadeIn(0);
                            }, 550)
                            setTimeout(() => {
                                let aux = circles;
                                aux[1] = "circleHiddenAn circleHiddenBox";
                                setCircles(s => ([...aux]))
                                quitarFadeIn(1);
                            }, 1050)
                            setTimeout(() => {
                                let aux = circles;
                                aux[2] = "circleHiddenAn circleHiddenBox";
                                setCircles(s => ([...aux]))
                                quitarFadeIn(2);
                            }, 1550)
                            setTimeout(() => {
                                let aux = circles;
                                aux[3] = "circleHiddenAn circleHiddenBox";
                                setCircles(s => ([...aux]))
                                quitarFadeIn(3);
                            }, 2050)
                            setTimeout(() => {
                                let aux = circles;
                                aux[4] = "circleHiddenAn circleHiddenBox";
                                setCircles(s => ([...aux]))
                                quitarFadeIn(4);
                            }, 2550)
                            setTimeout(() => {
                                let aux = circles;
                                aux[5] = "circleHiddenAn circleHiddenBox";
                                setCircles(s => ([...aux]))
                                quitarFadeIn(5);
                            }, 3050)
                        }}>

                        <Card.Img style={{borderRadius: '50%', backgroundColor: 'rgba(0, 0, 0, 0.0)'}} src={img} />
                    </Card>
                </Col>
            </Row>

            <Modal show={modal} onHide={e => {
                setModal(false)
                setCircles(['circleHidden', 'circleHidden', 'circleHidden', 'circleHidden', 'circleHidden', 'circleHidden'])
            }}>
                <Modal.Header closeButton>
                    <Modal.Title style={{marginLeft: 'auto'}}>My technologies</Modal.Title>
                </Modal.Header>

                <Modal.Body className="cardBody">{technologies.map((t, i) => {
                    if (i === 3) { // react
                        return (<div key={i} id={t} className={circles[i]}>
                            <img className="imgReact" src={imagenes[i][0]} alt="React"/>
                            <img className="imgReact2" src={imagenes[i][0]} alt="React2"/>
                            <img className="imgReactH" src={imagenes[i][1]} alt="ReactHex"/>
                        </div>)
                    }
                    return <div key={i} id={t} className={circles[i]}><img className="imgCircles" src={imagenes[i]} alt={t}/></div>
                })}</Modal.Body>
            </Modal>
        </Container>
    )
}