import { Link } from 'react-router-dom';
import { Navbar, Nav, Button, Row, Col, Container } from 'react-bootstrap';
import './NavBar.css';
import myself from '../../image/myself.png';

export default function NavBar(props) {
    let history = props.history
    let routes = [
        {
            name: 'About',
            dir: '/about'
        },
        {
            name: 'Resume',
            dir: '/resume'
        },
        {
            name: 'Portfolio',
            dir: '/portfolio'
        }/* ,
        {
            name: 'Contact',
            dir: '/contact'
        } */
    ]

    return (
        <Container>
        <Navbar fixed="top" bg="dark" variant="dark" expand="lg">
            <Navbar.Brand onClick={e => {
                e.preventDefault();
                props.history.push('/');
            }}>
            <img
                src={myself}
                width="55"
                height="55"
                className="d-inline-block align-top imgNavBar"
                alt="Myself"
            />
            </Navbar.Brand>
            <Navbar.Text className="ml-lg-4"><Link to="/" onClick={e => {
                if (document.getElementsByClassName('navbar-toggler-icon')[0].offsetParent !== null && // navbar not expanded
                document.getElementsByClassName('navbar-collapse')[0].offsetParent !== null) { // and buttons box expanded
                    document.getElementsByClassName('navbar-toggler')[0].click() // close on click button
                }
            }}><h4>Gonzalo D. Rodriguez</h4></Link></Navbar.Text>
            
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="basic-navbar-nav">
                <Row className="ml-lg-auto">
                <Nav className="mr-auto">
                {routes.map((r, i) => <Col key={i} className="mt-2 mt-lg-0"><Nav.Item className="ml-3"><Button onClick={e => {
                    e.preventDefault()
                    history.push(r.dir)
                    
                    if (document.getElementsByClassName('navbar-toggler-icon')[0].offsetParent !== null) { // navbar not expanded
                        document.getElementsByClassName('navbar-toggler')[0].click() // close on click button
                    }
                }}>{r.name}</Button></Nav.Item></Col>)}
                </Nav>
                </Row>
            </Navbar.Collapse>
        </Navbar>
        </Container>
    )
}